<template>
	<div class="panel">
		<h2>5月到11月数据统计</h2>
		<div ref="lineChart2" class="chart">图表</div>
		<div class="panel-footer"></div>
	</div>
</template>

<script>
	export default {
		name: 'LinePanel2',
		data() {
			return {
				list1: [],
				list2: [],
				list3: []
			}
		},
		methods: {
			drawChart() {
				let chart = this.$echarts.getInstanceByDom(this.$refs.lineChart2);
				if (!chart) {
					chart = this.$echarts.init(this.$refs.lineChart2);
					this.$once('hook:beforeDestroy', () => {
						this.$echarts.dispose(chart)
					})
				}
				let option = {
					tooltip: {
						trigger: 'axis',
						backgroundColor: "rgba(50, 50, 50, 0.7)",
						borderWidth: 0,
						textStyle: {
							color: "#ffffff"
						},
						hideDelay: 0
					},
					legend: {
						right: '10%',
						textStyle: {
							color: '#4c9bfd'
						},
						data: ['数据量', '点击量', '购买量']
					},
					grid: {
						left: "2",
						top: "30",
						right: "12",
						bottom: "10",
						containLabel: true
					},
					xAxis: [{
						type: 'category',
						boundaryGap: false,
						data: ["5月", "6月", "7月", "8月", "9月", "10月", "11月"],
						axisLabel: {
							color: "#fff"
						},
						axisLine: {
							lineStyle: {
								color: "rgba(255,255,255,.2)"
							}
						}
					}],
					yAxis: [{
						type: 'value',
						axisTick: {
							show: false
						},
						axisLine: {
							lineStyle: {
								color: "rgba(255,255,255,.1)"
							}
						},
						axisLabel: {
							color: "#fff"
						},
						splitLine: {
							lineStyle: {
								color: "rgba(255,255,255,.1)"
							}
						}
					}],
					series: [{
							name: '数据量',
							type: 'line',
							smooth: true,
							lineStyle: {
								color: "#ff3333",
								width: 2
							},
							areaStyle: {
								color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1,
									[{
											offset: 0,
											color: "rgba(255, 51, 51, 0.4)"
										},
										{
											offset: 0.8,
											color: "rgba(255, 51, 51, 0.1)"
										}
									],
									false
								),
								shadowColor: "rgba(0, 0, 0, 0.1)"
							},
							emphasis: {
								focus: 'series'
							},
							symbol: "circle",
							symbolSize: 8,
							itemStyle: {
								color: "#ff3333",
								borderColor: "rgba(221, 220, 107, .1)",
								borderWidth: 12
							},
							showSymbol: false,
							data: this.list1
						},
						{
							name: '点击量',
							type: 'line',
							smooth: true,
							lineStyle: {
								color: "#0184d5",
								width: 2
							},
							areaStyle: {
								color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1,
									[{
											offset: 0,
											color: "rgba(1, 132, 213, 0.4)"
										},
										{
											offset: 0.8,
											color: "rgba(1, 132, 213, 0.1)"
										}
									],
									false
								),
								shadowColor: "rgba(0, 0, 0, 0.1)"
							},
							emphasis: {
								focus: 'series'
							},
							symbol: "circle",
							symbolSize: 8,
							itemStyle: {
								color: "#0184d5",
								borderColor: "rgba(221, 220, 107, .1)",
								borderWidth: 12
							},
							showSymbol: false,
							data: this.list2
						},
						{
							name: '购买量',
							type: 'line',
							smooth: true,
							lineStyle: {
								color: "#00d887",
								width: 2
							},
							areaStyle: {
								color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1,
									[{
											offset: 0,
											color: "rgba(0, 216, 135, 0.4)"
										},
										{
											offset: 0.8,
											color: "rgba(0, 216, 135, 0.1)"
										}
									],
									false
								),
								shadowColor: "rgba(0, 0, 0, 0.1)"
							},
							emphasis: {
								focus: 'series'
							},
							symbol: "circle",
							symbolSize: 5,
							itemStyle: {
								color: "#00d887",
								borderColor: "rgba(221, 220, 107, .1)",
								borderWidth: 12
							},
							showSymbol: false,
							data: this.list3
						}
					]
				};
				chart.setOption(option);
				window.addEventListener("resize", chart.resize);
			}
		},
		mounted() {
			this.$axios.get(process.env.BASE_URL + 'expend/month.dat').then(resp => {
				for (let i = 0; i < resp.data.length; i++) {
					this.list1.push(resp.data[i][1]);
				}
				this.$axios.get(process.env.BASE_URL + 'expend/month2.dat').then(resp => {
					for (let i = 0; i < resp.data.length; i++) {
						this.list2.push(resp.data[i][1]);
					}
					this.$axios.get(process.env.BASE_URL + 'expend/month3.dat').then(resp => {
						for (let i = 0; i < resp.data.length; i++) {
							this.list3.push(resp.data[i][1]);
						}
						this.drawChart();
					})
				})
			})
		}
	}
</script>

<style>
</style>
